@charset "UTF-8";
.Product td {
  padding: 0 0 85px; }

.Product .ProductImage {
  width: 72px;
  height: 72px;
  margin-right: 12px; }
  .Product .ProductImage img {
    width: 100%; }

.Product .Title {
  font-size: 20px;
  color: #555; }

.Product .Price {
  color: #888; }

.Product .Controls input {
  border: 1px solid #cecece; }

@media screen and (min-width: 768px) {
  .Product .Controls {
    width: 100px; } }

@media screen and (min-width: 768px) {
  .Product .Total {
    width: 70px; } }

.Product .Delete {
  background-color: #c1c1c1;
  width: 22px;
  height: 22px;
  margin-left: 10px;
  font-size: 14px;
  color: #fff;
  border-radius: 50%; }

@media screen and (max-width: 767px) {
  .Product .Title {
    display: none; }
  .Product .Price {
    position: relative; }
    .Product .Price::before {
      content: 'Цена';
      position: absolute;
      top: -5px;
      font-size: 14px; }
  .Product .Controls {
    max-width: 65px; }
    .Product .Controls > div {
      position: relative;
      padding-left: 0;
      padding-right: 0;
      margin-right: 10px; }
      .Product .Controls > div::before {
        content: 'Кол-во';
        color: #888;
        position: absolute;
        top: -31px;
        left: 0;
        font-size: 14px;
        min-width: 50px; }
    .Product .Controls input {
      max-width: inherit; }
    .Product .Controls button {
      display: none; }
  .Product .Total {
    position: relative; }
    .Product .Total::before {
      content: 'Сумма';
      color: #888;
      position: absolute;
      top: -5px;
      left: 0;
      font-size: 14px; } }

@media screen and (max-width: 340px) {
  .Product .Controls > div::before {
    font-size: 12px; }
  .Product .Total::before, .Product .Price::before {
    font-size: 12px; } }

.MobileTitle {
  display: none; }

@media screen and (max-width: 767px) {
  .Product td {
    padding-bottom: 35px; }
  .Product:last-child td {
    padding-bottom: 10px; }
  .MobileTitle {
    display: table-row; }
    .MobileTitle .MobileTitleCell {
      color: #2e2eec;
      padding-bottom: 20px; } }
